import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { PurchaseReturnInvoiceComponent } from './purchase-return-invoice.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { DetailPurchaseComponent } from './detail-purchase/detail-purchase.component';
import { EditPurchaseComponent } from './edit-purchase/edit-purchase.component';
import { DetailReturnSupplierComponent } from './detail-return-supplier/detail-return-supplier.component';
import { EditReturnSupplierComponent } from './edit-return-supplier/edit-return-supplier.component'; 
import { BillType2Module } from 'src/app/sales/components/bill-type/bill-type-2/bill-type2.module';
import { BillType10Module } from 'src/app/sales/components/bill-type/bill-type-10/bill-type10.module';
const routes: Routes = [
  {
    path: '',
    component: PurchaseReturnInvoiceComponent
  } ,
  {
    path: 'edit-purchase',
    component: EditPurchaseComponent
  } ,
  {
    path: 'detail-purchase',
    component: DetailPurchaseComponent
  } ,
  {
    path: 'detail-return-supplier',
    component: DetailReturnSupplierComponent
  } ,
  {
    path: 'edit-return-supplier',
    component: EditReturnSupplierComponent
  } 
]


@NgModule({
  declarations: [PurchaseReturnInvoiceComponent, DetailPurchaseComponent, EditPurchaseComponent, DetailReturnSupplierComponent, EditReturnSupplierComponent,  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    BillType2Module,
    BillType10Module
  ]
})
export class PurchaseReturnInvoiceModule { }
