import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-purchase-return-invoice',
  templateUrl: './purchase-return-invoice.component.html',
  styleUrls: ['./purchase-return-invoice.component.scss']
})
export class PurchaseReturnInvoiceComponent implements OnInit {
  public date: Date[] = [new Date(), new Date()];
  public searchValue: string = ''
  public invoiceShow: any[] = []
  private invoiceSegment: any[] = []
  private invoiceFilter: any[] = []
  public invoices: any[];
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  tableHeight: string;
  status: any;
  totalValInvoice: number = 0;
  listTabs = ['Entered', 'Returned']
  suppliers = this.vhQuerySales.getlocalSuppliers()
  constructor(
    private vhQuery: VhQuery,
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    private vhAuth: VhAuth,
    private cdRef: ChangeDetectorRef,
    private datePipe: DatePipe,
    private fncService: FunctionService
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
  }

  ngOnInit() {
    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue
      this.filterType = this.dataRestore.filterType
      this.date = [new Date(this.dataRestore.startTime), new Date(this.dataRestore.endTime)];
      this.selectedIndex = this.dataRestore.selectedIndex
      this.invoices = this.dataRestore.invoice
      this.segmentChanged(this.selectedIndex);
      if (this.dataRestore.reload) this.getInvoices();
    }
    else { this.filterType = 'all'; }
    // this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => this.getInvoices())
  }
  /**
   * get danh sách hóa đơn nhập hàng từ DB
   * @example this.getInvoices()
   */
  getInvoices() {
    this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => {
      this.vhQuerySales.getBills_byFields( { date: { $gte: new Date(this.date[0].setHours(0, 0, 0)), $lte: new Date(this.date[1].setHours(23, 59, 59, 59)) }, bill_type: { $in: [2, 10, 22, 24] }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id } }, {})
        .then((invoice: any[]) => {

          this.invoices = invoice.map(item => {
            return {
              ...item,
              partner_name: this.renderPartner(item.id_supplier),
              payment_name: this.renderPayment(item.payment_type, item.id_wallet),
              status: this.renderStatus(item.bill_type),
              name_employee: this.vhAuth.getlocalEmployee(item.id_employee).name,
              sumPrice: item.total * (1 + item.tax / 100)
            }
          })
          this.segmentChanged(this.selectedIndex);
          this.status = "Not found"
          this.vhComponent.hideLoading(0);
        }, error => {
          this.vhComponent.hideLoading(0);
        })
    })
  }
  /**
   * trả về tên loại trạng thái bill
   * @param bill_type 
   * @returns string
   * @example let bill_name = this.renderStatus(2)
   */
  renderStatus(bill_type: 2 | 10 | 22 | 24) {
    switch (bill_type) {
      case 22: return `${this.lang.translate('Cancelled')} (${this.lang.translate('Purchase')})`
      case 24: return `${this.lang.translate('Cancelled')} (${this.lang.translate('Returned')})`
      // case 6: return this.lang.translate('Temporary')
      case 2: return this.lang.translate('Entered')
      default: return this.lang.translate('Returned')
    }
  }
  /**
   * trả về tên ncc của bill
   * @param id_partner 
   * @returns string
   * @example let supplier_name = this.renderPartner('DNIKWUJHNBDUYWGU') 
   */
  renderPartner(id_partner: string) {
    let supplier: string = this.vhQuerySales.getlocalSupplier(id_partner).name

    return supplier ? supplier : this.lang.translate("Not found")
  }
  /**
   * router qua trang chi tiết bill
   * @example this.goToDetail('DMNWIDHNBIUWHGDUWY')
   */
  goToDetail(data) {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType, startTime: this.date[0], endTime: this.date[1], selectedIndex: this.selectedIndex, invoice: this.invoices }
    if(data.bill_type == 2) this.router.navigate(["/sales/dashboard/manage/invoice/purchase-return-invoice/detail-purchase"], { state: { id: data._id, dataRestore } })
    if(data.bill_type == 10) this.router.navigate(["/sales/dashboard/manage/invoice/purchase-return-invoice/detail-return-supplier"], { state: { id: data._id, dataRestore } })
  }
  public filterType = 'all';

  /**
   * lọc hóa đơn theo loại hóa đơn
   * @example this.sortFilter(1)
   */
  sortFilter(filterType) {
    if (filterType == 'all') {
      this.invoiceFilter = this.invoiceSegment
    }
    else {
      this.invoiceFilter = this.invoiceSegment.filter(item => item.id_supplier == filterType)
    }
    this.searchBill(this.searchValue)
  }
  /**
   * lọc bill theo ['bill_code', 'partner_name', 'payment_name', 'status', 'employee_name']
   * @example this.searchBill('')
   */
  searchBill(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoiceFilter, ['bill_code', 'partner_name', 'payment_name', 'status'])
    } else this.invoiceShow = [...this.invoiceFilter]

    this.totalValInvoice = this.getTotal()
  }
  /**
   * trả về tổng daonh số của tất cả hóa đơn đang hiển thị
   * @returns 
   */
  getTotal() {
    return this.invoiceShow.reduce((prev: number, next) => prev + next.total * (1 + next.tax / 100), 0);
  }

  ngAfterViewChecked() {
    if (document.querySelector("#purchase-return-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-tabs-nav") && document.querySelector(".invoice-header")) {
      this.tableHeight = document.querySelector("#purchase-return-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".invoice-header").clientHeight - document.querySelector(".ant-tabs-nav").clientHeight - 60 + "px";
      if (document.querySelector(".ant-table-pagination")) {
        this.tableHeight = document.querySelector("#purchase-return-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
          - document.querySelector(".invoice-header").clientHeight - document.querySelector(".ant-tabs-nav").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 60 + "px";
      }
    }
    this.cdRef.detectChanges();
  }
  /**
   * trả về tên phương thức thanh toán của đơn
   * @example let payment_name= this.renderPayment(1)
   */
  renderPayment(payment_type: 1 | 2 | 3, id_wallet: string = null) {
    switch (payment_type) {
      case 1: return this.lang.translate('Cash')
      case 2: return this.lang.translate('Debit')
      case 3: return this.vhQuerySales.getlocalWallet(id_wallet).name
    }
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/invoice"])
  }
  selectedIndex: number = 0;
  mess_result = ''
  /**
   * bắt sk thay đổi tab loại hóa đơn
   * @param value 
   */
  segmentChanged(value) {
    if (this.invoices) {
      switch (value) {
        case 0: {
          if (this.vhAuth.checkMyPermission("purchase_enable_view_bill")) { this.invoiceSegment = this.invoices.filter(item => item.bill_type == 2); this.mess_result = 'Not invoice yet'; }
          else { this.invoiceSegment = []; this.mess_result = 'You do not have this rights'; }
          break;

        }
        case 1: {
          if (this.vhAuth.checkMyPermission("return_supplier_enable_view_bill")) { this.invoiceSegment = this.invoices.filter(item => item.bill_type == 10); this.mess_result = 'Not invoice yet'; }
          else { this.invoiceSegment = []; this.mess_result = 'You do not have this rights'; }
          break;
        }
        // case 2: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 6); break;
        // case 2: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 22); break;
        // case 3: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 24); break;
      }
      this.sortFilter(this.filterType);
    }
  }

  /**
   * bắt sk thay đổi thời gian xem hóa đơn
   * @param event array<date>
   * @example this.changeTime([2023-06-23T07:12:35.157Z,2023-06-23T07:12:35.157Z])
   */
  changeTime(event) {
    if (new Date(event[1].setHours(0, 0, 0, 0)).getTime() - new Date(event[0].setHours(0, 0, 0, 0)).getTime() <= this.fncService.checkDateReport() * 86000000) {
      //this.getTransferList();
    } else {
      this.vhComponent.alertMessageDesktop("error", this.lang.translate("The time limit to view is") + " " + this.fncService.checkDateReport() + " " + this.lang.translate("days"))
      this.date = [new Date(event[0]), new Date(event[0].getTime() + this.fncService.checkDateReport() * 86000000)]
    }
  }

  /**
   * lưu mã đơn vào RAM
   * @example this.copyBillCode('230623A001')
   */
  copyBillCode(billCode) {
    this.vhComponent.copyValue(billCode)
  }
  /**
   * format lại định dạng hiển thị ngày tháng để tải về file excel
   * @param date 
   * @param type 
   * @returns string
   * @example let date = this.format_date(2023-06-23T07:12:35.157Z, 'dd/MM/yyyy')
   */
  format_date(date, type) {
    return this.datePipe.transform(date, type)
  }
  /**
   * trả về tên loại bill
   * @param selectedIndex 
   * @returns string
   * @example let bill_name = this.renderNameExcel(0)
   */
  renderNameExcel(selectedIndex) {
    switch (selectedIndex) {
      case 0: return `${this.lang.translate('Purchase invoice')} (${this.lang.translate('Entered')})`
      case 1: return `${this.lang.translate('Return supplier invoice')} (${this.lang.translate('Returned')})`
      case 2: return `${this.lang.translate('Purchase invoice')} (${this.lang.translate('Cancelled purchase')})`
      case 3: return `${this.lang.translate('Return supplier invoice')} (${this.lang.translate('Cancelled return')})`
    }

  }
  //xuất excel hóa đơn
  export() {
    let data = [];
    let name = `${this.renderNameExcel(this.selectedIndex)}_${this.lang.translate("_from")}_${this.format_date(this.date[0], 'dd_MM_yyyy')}_${this.lang.translate("_to")}_${this.format_date(this.date[0], 'dd_MM_yyyy')}_${this.vhAlgorithm.changeAlias(this.vhQuerySales.getDefaultBranch().name).replace(/\s/g, "_")}`;
    let total: any = new Object();
    total[this.lang.translate('Date')] = ''
    total[this.lang.translate('Invoice code')] = this.lang.translate('Total')
    total[this.lang.translate('Customer')] = ''
    total[this.lang.translate('Payment method')] = ''
    total[this.lang.translate('Employee')] = ''
    total[this.lang.translate('Note')] = ''
    total[this.lang.translate('Total')] = this.vhAlgorithm.vhcurrencyunit(this.totalValInvoice)
    data.push(total)
    for (let bill of this.invoiceShow) {
      let item: any = new Object();
      item[this.lang.translate('Date')] = this.format_date(bill.date, 'dd/MM/yyyy HH:mm')
      item[this.lang.translate('Invoice code')] = bill.bill_code
      item[this.lang.translate('Customer')] = bill.partner_name
      item[this.lang.translate('Payment method')] = `${bill.payment_name} ${(bill.payment - bill.total < 0 && bill.payment_type != 2) ? `- ${this.lang.translate('Rest')} ${this.vhAlgorithm.vhcurrencyunit((bill.total * (1 + bill.tax / 100)) - bill.payment)}` : ''}`
      item[this.lang.translate('Employee')] = bill.name_employee
      item[this.lang.translate('Note')] = bill.note
      item[this.lang.translate('Total')] = this.vhAlgorithm.vhcurrencyunit(bill.total * (1 + bill.tax / 100))
      data.push(item);
    }
    this.vhAlgorithm.exportXLSX(data, name)
  }

  /** hàm sort cho các cột */
  dateCol = false;
  bill_codeCol = false;
  partner_nameCol = false;
  payment_nameCol = false;
  name_employeeCol = false;
  sumPriceCol = false;
  /** Hàm thực hiện sắp xếp theo collection
   *
   * @param colName       // tên cột muôn sắp xếp
   */
  sortTable(colName) {
    switch (colName) {
      case 'date':
        if (this.dateCol) {
          this.invoiceShow = this.vhAlgorithm.sortDatebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortDatebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'bill_code':
        if (this.bill_codeCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'partner_name':
        if (this.partner_nameCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'payment_name':
        if (this.payment_nameCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'name_employee':
        if (this.name_employeeCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'sumPrice':
        if (this.sumPriceCol) {
          this.invoiceShow = this.vhAlgorithm.sortNumberbyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortNumberbyDESC([...this.invoiceShow], colName);
        }
        break;
    }
  }

  /**
   * lọc nhà cung cấp theo tên
   * @param value 
   * @example this.searchFilter('')
   */
  searchFilter(value) {
    this.suppliers = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.vhQuerySales.getlocalSuppliers(), ['name'])
  }
}
